.container { 
  padding: 0.2em; 
}

.container.grid {
  width: 50%;
}

.container.list {
  width: 100%;
}
.video {
  width: 100%; 
  height: 100%; 

  border: 1px solid lightgray; 
  display: flex;  
  list-style: none;  
  align-items: center; 
  cursor: pointer; 
  transition: transform 250ms ease-in; 
  box-shadow: 3px 3px 5px 0px rgba(191, 191, 191, 0.53) ;
} 
 
.video:hover{
  transform: scale(1.02); 
}

.thumbnail {
  width: 40%; 
  height: 100%; 
}

p {
  margin: 0; 
}
.metadata { 
  margin-left: 0.4em;
}
.title {
  font-size: 1.2rem; 
}

.channel {
  font-size: 0.9rem;
}